                    <img :src="item" >
                </dt>

                <dd>
                    <div><p>{{msg.intro[index]}}</p></div>
                    <div><span>{{msg.origin.com[index]}}</span><span>{{msg.origin.num[index]}}</span></div>
                </dd>
            </dl>
        </div>
