<script>
    export default {
        name: "passage",
        props:["msg"],
        // data(){
        //     return {
        //         imgs:['../../../static/pass1.png','../../../static/pass2.png','../../../static/pass3.png','../../../static/pass4.png','../../../static/pass5.png','../../../static/pass6.png','../../../static/pass7.png'],
        //         intro:['1615','16X,?','App()','!16X16',',APP','EP52 lite:,',''],
        //         origin:{com:['','','','-','','',''],num:['89899','89899','14899','38335','39439','9839','29391']}
        //     }
        // }
    }
</script>

<style scoped>
  .passage{
      display: flex;
