  .passage>.top{
      height:.68rem;
      line-height: .68rem;
  }
  .passage>.top>h3{
      font-size:.20rem;
      color:#000;
  }

  .passage>.bottom>dl{
      height:1.09rem;
      margin-bottom:.27rem;
      display: flex;
      width:100%;

  }
  .passage>.bottom>dl>dt{
      width:1.62rem;
      height:100%;
  }
  .passage>.bottom>dl>dt>img{
      height:100%;
      width:100%;
  }
  .passage>.bottom>dl>dd{
      display: flex;
      flex:1;
      flex-direction: column;
      justify-content:space-evenly;
      padding-left:.19rem;
  }
  .passage>.bottom>dl>dd>div:nth-of-type(1){

      width:100%;
  }

  .passage>.bottom>dl>dd>div:nth-of-type(1)>p{
      font-size: .22rem;
      color:#000;
  }

  .passage>.bottom>dl>dd>div:nth-of-type(2)>span{
      font-size: .11rem;
      color:#777;
      padding-right:.22rem;
  }

</style>



